@import "sass/index";

$buttonsIndent: 10px;

.root {
  width: 100%;
}

.optionWrapper {
  background-color: use(--template-gameCell-controller);
  & > div {
    margin: 0;
  }
  padding-top: $buttonsIndent;
  padding-bottom: $buttonsIndent;
  & > div > button > span > svg path {
    fill: use(--template-button-option-color);
  }
}

.hrSmall {
  width: 100%;
  height: 1px;
  background-color: transparent;
}

.hrBig {
  width: 100%;
  height: 10px;
  background-color: transparent;
}

.statisticsWrapper {
  margin-top: 10px;
}
