@import "config/themes/_video-poker";

$inputHeight: 60px;
$inputFieldHeight: 40px;

.wrapper {
  display: flex;
  align-items: flex-end;
  width: 100%;
  height: $inputHeight;
  position: relative;
  overflow: hidden;
  background: use(--template-anyContent-colors-third);
  border: 2px $slate-blue solid;
  border-radius: 8px;

  & > div > label {
    line-height: 16px;
    font-size: 10px;
    font-family: "Varela Round";
    color: use(--template-statisticsBlock-tab-color) !important;
    opacity: 0.5;
    left: 20px;
    top: -10px;
  }

  & > div {
    margin-top: 0;
  }
}

.root {
  margin-top: 0;
  border: 0;
  background: transparent;
  border-radius: 8px;
  height: $inputFieldHeight;
  box-shadow: none !important;
  font-size: 18px;
  color: use(--template-statisticsBlock-tab-color);
  & > input {
    padding-left: 10px;
  }
}

.disabled {
  color: use(--template-statisticsBlock-tab-color);
}
