@import "sass/index";

$buttonBorderRadius: 6px;

.button {
  height: 100%;
  background-color: #2d2e3d;
  font-family: "Gilroy";
  color: rgba($white, 0.5);
  padding-right: 0;
  padding-left: 0;
  min-width: 47px;
}

.btnLeft {
  border-radius: $buttonBorderRadius 0px 0px $buttonBorderRadius;
  margin-right: 1px;
}

.btnRight {
  border-radius: 0px $buttonBorderRadius $buttonBorderRadius 0px;
}

.btnCenter {
  border-radius: $buttonBorderRadius;
  margin-right: 1px;
}

.mobileInputBtn {
  position: relative;
  width: 100%;
}
