@import "sass/index";
@import "config/themes/_video-poker";

@import "./components/DesktopInput/style.module.scss";

$btnMargin: 10px;

.rootWrapper {
  width: 100%;
  padding-left: 60px;
  padding-right: 60px;
  padding-top: 30px;
  @media (max-width: 500px) {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.currencyBlock {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 1fr;
  column-gap: 20px;
  @media (max-width: 1200px) {
    grid-template-columns: none;
    grid-template-rows: 1fr 1fr;
    row-gap: 10px;
  }
  @media (max-width: 500px) {
    row-gap: 0;
  }
}

.selectWrapper {
  & > div {
    height: 100%;
  }
}

.inputWrapper {
  position: relative;
}

.desktopInput {
  @media (max-width: 500px) {
    display: none;
  }
}

.btnWrapper {
  position: absolute;
  bottom: $btnMargin;
  right: $btnMargin;
  height: $inputHeight - $btnMargin * 2;
  display: flex;
  align-items: center;
}

.betButtons {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  margin-top: 40px;
  padding-bottom: 50px;
}

.betButton {
  background: linear-gradient(276.53deg, #2e2eed -93.34%, #4e4ef9 82.6%);
  box-shadow: inset 0px -2px 0px rgba(0, 0, 0, 0.135107);
  color: white;
  font-size: 22px;
  border-radius: $primaryBorderRadius;
  padding-left: 78px;
  padding-right: 78px;
  padding-top: 7px;
  padding-bottom: 7px;
}

.highlight {
  color: white;
}

.inputLabel {
  color: rgba($white, 0.7) !important;
}

.warning {
  top: -110px;
}
