@import "sass/index";

$inputHeight: 60px;
$inputFieldHeight: 40px;

$buttonsWrapperIndent: 10px;

.wrapper {
  display: none;
  width: 100%;
  max-width: 500px;
  overflow: hidden;
  background: rgba(20, 25, 48, 0.3);
  border: 2px $slate-blue solid;
  border-radius: 8px;

  & > div > label {
    line-height: 16px;
    font-size: 13px;
    font-family: "Varela Round";
    color: use(--template-statisticsBlock-tab-color) !important;
    opacity: 0.5;
    top: 10px;
    left: 11px;
  }

  & > div {
    margin-top: 0;
  }

  @media (max-width: 500px) {
    display: block;
  }
}

.root {
  width: 100%;
  margin-top: 0;
  border: 0;
  background: transparent;
  border-radius: 8px;
  height: $inputFieldHeight;
  box-shadow: none !important;
  font-size: 18px;
  color: use(--template-statisticsBlock-tab-color);
  padding-left: 20%;
  & input {
    // padding-right: 9px;
    text-align: end;
  }
}

.disabled {
  color: rgba($white, 0.6);
}

.buttonsWrapper {
  display: flex;
  padding-left: $buttonsWrapperIndent;
  padding-right: $buttonsWrapperIndent;
  padding-bottom: $buttonsWrapperIndent;
}
